import {Component, ContentChild, Input, ViewEncapsulation} from '@angular/core';
import {MatJumbotronContentComponent} from './content/mat-jumbotron-content.component';

@Component({
  selector: 'mat-jumbotron',
  template: `
    <mat-card fxLayout="row" fxLayoutAlign="center center">

      <mat-card-content fxLayout="row"
                        fxFlex
                        fxLayout.xs="column"
                        fxLayoutGap="2rem"
                        fxLayoutAlign="center center">
        <!--main image-->
        <img *ngIf="imgURL"
             mat-card-xl-image
             [src]="imgURL">

        <!--content-->
        <div fxLayout="column" [fxFlex]="imgURL ? 50 : 100">
          <ng-container [ngTemplateOutlet]="jumbotronContent.content"></ng-container>
        </div>
      </mat-card-content>

    </mat-card>
  `,
  styles: [`
    :host{display:block}:host ::ng-deep mat-card{background:#e9ecef!important}:host ::ng-deep mat-card img{-o-object-fit:cover;object-fit:cover}:host ::ng-deep mat-card h1{font-weight:400!important}:host ::ng-deep mat-card p{margin-top:0;margin-bottom:1rem;font-size:1.4rem}
  `],
  encapsulation: ViewEncapsulation.Emulated
})
export class MatJumbotronComponent {

  @ContentChild(MatJumbotronContentComponent, {static: false}) jumbotronContent: MatJumbotronContentComponent;

  @Input()
  imgURL: string;

  constructor() {
  }

}
